import React from "react"
import { useState } from "react"
import * as style from "../styles/DashboardMenu.module.scss"
import { HOST_URL } from "./App"

export default function Thumbnail(props: any) {
    const [src, setSrc] = useState<string>(`${HOST_URL}${props.src}`)
    const width = 360
    const height = 203

    return <img className={style.thumbnail} src={src} width={width} height={height}></img>
}
